/* General App Styling */
.App {
    font-family: Arial, sans-serif;
    padding: 20px;
    margin: 0 auto;
    max-width: 1200px;
  }
  
  h1 {
    text-align: center;
    margin-bottom: 40px;
  }
  
  .app-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  
  /* Form Styling */
  .refinance-form {
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 35%;
  }
  
  .refinance-form h3 {
    margin-bottom: 15px;
    font-size: 1.25rem;
  }
  
  .form-group {
    margin-bottom: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .form-group label {
    flex: 1;
    font-size: 0.95rem;
    margin-right: 10px;
  }
  
  .form-group input {
    flex: 2;
    padding: 8px;
    font-size: 0.95rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  button {
    background-color: #007BFF;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  /* Loan Comparison Section Styling */
  .loan-comparison {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 30%;
  }
  
  .loan-comparison h3 {
    margin-bottom: 20px;
  }
  
  .loan-comparison p {
    margin: 10px 0;
    font-size: 1rem;
  }
  
  /* Graph Styling */
  .graph-container {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 30%;
  }
  
  .graph-container canvas {
    width: 100% !important;
    height: auto !important;
  }
  